.sr-only {
	position: absolute;
	width: 1px;
	height: 1px;
	padding: 0;
	margin: -1px;
	overflow: hidden;
	clip: rect(0, 0, 0, 0);
	white-space: nowrap;
	border-width: 0;

	&.--focusable {
		&:focus,
		&:focus-visible {
			position: fixed;
			inset: 0.5em auto auto 0.5em;
			z-index: 100000;
			width: auto;
			height: auto;
			margin: 0;
			padding: 0.9375em 1.4375em 0.875em;
			overflow: visible;
			clip: auto;
			white-space: normal;
			outline-offset: -0.0625em;
			background: hsl(240, 3.4%, 94.3%);
			box-shadow: 0 0 0.125rem 0.125rem hsla(0, 0%, 0%, 0.6);
			color: hsl(206.9, 67.8%, 41.4%);
			font-size: 0.875rem;
			font-weight: 600;
			line-height: normal;
			text-decoration: none;
		}
	}
}

.draad-adreszoeker {
	container-name: adreszoeker;
	container-type: inline-size;

	*,
	*::before,
	*::after {
		box-sizing: border-box;
	}

	form {
		display: grid;
		grid-template-columns: 1fr;
		gap: 8px;
		width: 100%;
		margin: 0;
		position: relative;
	}

	&__filters {
		margin-bottom: 1em;
		display: grid;
		grid-template-columns: 1fr;
		gap: 16px 8px;
		width: 100%;
		margin-top: 50px;

		@container adreszoeker (min-width: 680px) {
			grid-template-columns: 1fr 1fr;
		}

		label {
			display: block;
			margin-block-end: 4px;
		}

		input[list],
		input[type='number'] {
			border: 1px solid #757575;
			background-color: #ffffff;
			padding: 13px 20px;
			color: #4b4b4b;
			font-size: 16px;
			line-height: 1.73;
			width: 100%;

			&::-webkit-input-placeholder {
				opacity: 0.7;
			}

			&::-moz-placeholder {
				opacity: 0.7;
			}

			&:-moz-placeholder {
				opacity: 0.7;
			}
		}

		input[type='submit'] {
			padding: 10px 20px;
			font-size: 18px;
			line-height: 1.66;
			color: #fff;
			display: inline-flex;
			margin-top: 12px;
			border-radius: 2px;
			border: none;
			cursor: pointer;
			width: fit-content;
			background: #1d6b34;
			transition: all 300ms ease;

			&:hover {
				color: #000;
				background: #f8d62d;

				.arrow-down {
					&:before {
						color: #000;
					}
				}
			}
		}

		input[type='number']:disabled {
			background-color: #d2d2d2;
		}

		.fcomplete-wrap {
			box-shadow: 0 5px 24px 0 rgb(0 0 0 / 21%);
			z-index: 1;
			border: 0;
			height: 200px;
			overflow-y: auto;

			.fcomplete-result {
				padding: 5px 16px;
				font-size: 16px;
				line-height: 1.66;
				color: #000;
				transition: all 300ms ease;

				&:hover {
					color: #fff;
					background: #1d6b34;
				}
			}
		}
	}
}
